import React from 'react';
import {Layout, Card, Icon} from 'antd';
import HeaderWrapper from './header';
import SiderWrapper from './sider';
import BreadCrumb from './bread';
import "./index.scss";

const {Content, Sider, Header, Footer} = Layout;

const BasicLayout = props => <Layout className="basic-layout">
  <HeaderWrapper/>
  <Layout>
    <Sider collapsible className="basic-sider" theme="light" width="180">
      <SiderWrapper/>
    </Sider>
    <Layout>
      <Content className="basic-content">
        <Card className="basic-card">
          <Layout>
            <Header className="basic-header"><BreadCrumb /></Header>
            <Content>{props.content}</Content>
            <div className="connect">
              <Icon type="customer-service" theme="filled" />
              <p>客服</p>
              <div className="connect-method">
                <img className="connect-qrcode" src="http://t.dmmup.com/uploads/1861795c12c690ef4df709abcaf73fc2.jpg" alt=""/>
                <p className="connect-title">微信扫描二维码，咨询客服</p>
              </div>
            </div>
          </Layout>
        </Card>
      </Content>
      <Footer className="basic-footer">Copyright&copy;2019  最终解释权归本公司所有</Footer>
    </Layout>
  </Layout>
</Layout>;

export default BasicLayout;