import React, {Component} from 'react';
import { Form, Input, Select, Table, Modal, message} from 'antd';
import Button from '../../../common/components/Button';
import axios from 'axios';

const {Option} = Select;

class NovelChannel extends Component {

	constructor(props) {
		super(props);
		this.addPlatform = this.addPlatform.bind(this);
		this.changePlatformInfoInput = this.changePlatformInfoInput.bind(this);
		this.submitPlatform = this.submitPlatform.bind(this);
		this.cancelAction = this.cancelAction.bind(this);
		this.editPlatform = this.editPlatform.bind(this);
		this.deletePlatform = this.deletePlatform.bind(this);
	}

	state = {
		platformList: [],
		emptyText: "",
		modalVisible: false,
		platformInfo: {
			platform: 1,
			gonghao_name: "",
			seruid: ""
		},
		title: "添加平台"
	};

	getPlatformList() {
		axios.get('/platform')
			.then(res => {
				let {code, msg, data} = res.data;
				if(code === 1) {
					this.setState({
						platformList: data
					})
				} else {
					this.setState({emptyText: msg, platformList: []})
				}
			})
	}

	addPlatform() {
		this.setState({
			modalVisible: true
		})
	}

	changePlatformInfoInput(value, name) {
		let {platformInfo} = this.state;
		platformInfo[name] = value;
		this.setState({platformInfo});
	}

	submitPlatform() {
		let {platformInfo} = this.state;
		if(!platformInfo._id) {
			this.createPlatform(platformInfo)
		} else {
			this.updatePlatform(platformInfo)
		}
	}

	createPlatform(platformInfo) {
		Modal.confirm({
			title: "您确认创建一条新的内容吗？",
			onOk: () => {
				axios.post('/platform', {...platformInfo})
					.then(res => {
						let {code, msg} = res.data;
						if(code === 1) {
							message.success(msg);
							this.getPlatformList()
							this.cancelAction()
						} else {
							message.error(msg)
						}
					})
			}
		})
	}

	updatePlatform(platformInfo) {
		Modal.confirm({
			title: "您确认保存这次修改吗？",
			onOk: () => {
				axios.put('/platform', {...platformInfo})
					.then(res => {
						let {code, msg} = res.data;
						if(code === 1) {
							message.success(msg);
							this.getPlatformList()
							this.cancelAction()
						} else {
							message.error(msg)
						}
					})
			}
		})
	}

	cancelAction() {
		this.setState({
			modalVisible: false,
			platformInfo: {
				platform: "",
				gonghao_name: "",
				seruid: ""
			}
		})
	}

	editPlatform(record) {
		this.setState({platformInfo: record, modalVisible: true})
	}

	deletePlatform(id) {
		Modal.confirm({
			title: "确定删除这条内容吗？",
			onOk: () => {
				axios.delete('/platform', {params: {id}})
					.then(res => {
						let {code, msg} = res.data;
						if(code === 1) {
							message.success(msg);
							this.getPlatformList();
						} else {
							message.eroor(msg)
						}
					})
			}
		})
	}

	UNSAFE_componentWillMount () {
		this.getPlatformList()
	}

	render() {
		let {platformList, emptyText, modalVisible, platformInfo, title} = this.state;
		let columns = [
			{
				dataIndex: "platform",
				key: "platform",
				title: "平台名称",
				align: "center",
				render: text => text === 1 ? "阅文" : (text === 2 ? "腾文" : "无")
			}, {
				dataIndex: "gonghao_name",
				key: "gonghao_name",
				title: "公号名称",
				align: "center"
			}, {
				dataIndex: "seruid",
				key: "seruid",
				title: "公号id",
				align: "center"
			}, {
				title: "操作",
				key: "action",
				align: "center",
				render: (text, record, index) => (
					<div>
						<Button onClick={() => this.editPlatform({...record})} buttonContent="编辑" />
						<Button onClick={() => this.deletePlatform(record._id)} background="danger" buttonContent="删除" />
					</div>
				)
			}
		];

		return (
			<div>
				<div className="container">
					<Button mb={20} size="default" onClick={this.addPlatform} background="success" buttonContent="添加" />
					<Table rowKey={record => record._id} columns={columns} dataSource={platformList} locale={{emptyText: emptyText}} size="small" />
				</div>
				<Modal title={title} visible={modalVisible} onOk={this.submitPlatform} onCancel={this.cancelAction}>
					<Form labelCol={{span: 6}} wrapperCol={{span: 14}}>
						<Form.Item label="平台名称">
							<Select value={platformInfo.platform} onChange={value => this.changePlatformInfoInput(value, "platform")}>
								<Option value={1}>阅文</Option>
								<Option value={2}>腾文</Option>
							</Select>
						</Form.Item>
						<Form.Item label="公号名称">
							<Input value={platformInfo.gonghao_name} onChange={e => this.changePlatformInfoInput(e.target.value, "gonghao_name")} />
						</Form.Item>
						<Form.Item label="公号id">
							<Input value={platformInfo.seruid} onChange={e => this.changePlatformInfoInput(e.target.value, "seruid")} />
						</Form.Item>
					</Form>
				</Modal>
			</div>
		)
	}
}

export default NovelChannel;