import axios from '../../../../utils/http';
import * as constants from './constants';
import { Modal, message } from 'antd';

export const getRandomMessageList = () => {
  return dispatch => {
    axios.get('/messageRandom')
      .then(res => {
        let {code, data} = res.data;
        if(code === 1) {
          dispatch({
            type: constants.GET_RANDOM_MESSAGE_LIST,
            data
          })
        } else {
          dispatch({
            type: constants.GET_RANDOM_MESSAGE_LIST,
            data: []
          })
        }
      })
  }
}

export const deleteRandomMessage = _id => {
  return dispatch => {
    
    Modal.confirm({
      title: "确定删除这条消息吗？",
      onOk: () => {
        axios.get('/messageRandom/del', {params: {_id}})
          .then(res => {
            let {code, msg} = res.data;
            if(code === 1) {
              message.success(msg);
              dispatch(getRandomMessageList())
            } else {
              message.error(msg)
            }
          })
      }
    })
  }
};

export const changeMessageValue = (value, name) => ({
  type: constants.CHANGE_MESSAGE_VAlUE,
  value, name
})

export const changeMessageType = value => ({
  type: constants.CHANGE_MESSAGE_TYPE,
  value
});

export const changeContentValue = (value, name) => ({
  type: constants.CHANGE_CONTENT_VALUE,
  value, name
})

export const addMessage = () => ({
  type: constants.ADD_MESSAGE
})

export const updateMessageItem = (item, index) => ({
  type: constants.UPDATE_MESSAGE_ITEM,
  item, index
})

export const deleteMessageItem = index => {
  return dispatch => {
    Modal.confirm({
      title: "确定删除这条内容吗？",
      onOk: () => {
        dispatch({
          type: constants.DELETE_MESSAGE_ITEM,
          index
        })
      }
    })
  }
}

export const saveMessage = () => ({
  type: constants.SAVE_MESSAGE_UPDATE
})

export const submitMessage = (info, history) => {
  return dispatch => {
    Modal.confirm({
      title: info._id ? "确定保存这侧修改吗？" : "确定创建一条新消息吗？",
      onOk: () => {
        axios.post("/messageRandom/" + (info._id ? "update" : "create"), {...info})
          .then(res => {
            let {code, msg} = res.data;
            if(code === 1) {
              message.success(msg);
              history.goBack()
            } else {
              message.error(msg)
            }
          })
      }
    })
  }
}

export const cancelAction = history => {
  return dispatch => {
    history.goBack()
  }
}

export const initRandomMsgInfo = info => ({
  type: constants.INIT_RANDOM_MSG_INFO,
  info
})

export const resetState = () => ({
  type: constants.RESET_STATE
})