import React, { Component } from 'react';
import { Form, Input, message } from 'antd';
import Button from '../../../common/components/Button';
import axios from '../../../utils/http';

class ToutiaoLiantiao extends Component {

	constructor(props) {
		super(props);
		this.changeTdUrlInput = this.changeTdUrlInput.bind(this);
		this.submitInput = this.submitInput.bind(this);
	}

	state = {
		td_url: "",
	};

	changeTdUrlInput(e) {
		let td_url = e.target.value;
		this.setState({ td_url });
	}

	submitInput() {
		let { td_url } = this.state;
		if (!td_url) {
			message.error("链接不能为空")
		} else {
			axios.post('/platform/liantiao', { td_url })
				.then(res => {
					let { msg } = res.data;
					message.success(msg);
					this.setState({ td_url: "" })
				})
		}
	}

	render() {
		let { td_url } = this.state;
		return (
			<div className="container">
				<Form labelCol={{ span: 2 }} wrapperCol={{ span: 14 }}>
					<Form.Item label="链接">
						<Input.TextArea autoSize={{ minRows: 3 }} spellCheck={false} value={td_url} onChange={this.changeTdUrlInput} allowClear />
					</Form.Item>
					<Form.Item label=" " colon={false}>
						<Button size="default" onClick={this.submitInput} background="success" buttonContent="提交" />
					</Form.Item>
				</Form>
			</div>
		)
	}
}

export default ToutiaoLiantiao;