import React, { Component } from 'react';
import { Table } from "antd";
import { connect } from "react-redux";
import { actionCreators } from './store';
import { Link } from "react-router-dom";
import Button from '../../../common/components/Button';

class RandomKefu extends Component {

  componentDidMount() {
    this.props.getRandomMessageList()
  }

  render() {
    let {randomMessageList, deleteRandomMessage} = this.props;
    const columns = [
      {
        key: "message_id",
        title: "消息ID",
        dataIndex: "message_id",
        align: "center"
      }, {
        key: "remarks",
        title: "消息名称",
        dataIndex: "remarks",
        align: "center"
      }, {
        key: "message_array",
        title: "随机消息数",
        dataIndex: "message_array",
        align: "center",
        render: text => text.length
      }, {
        title: "Action",
        align: "center",
        render: (text, record, index) => (
          <div>
            <Button buttonContent={<Link to={{pathname: "./update", query: {...record}}}>修改</Link>} />
            <Button background="danger" onClick={() => deleteRandomMessage(record._id)} buttonContent="删除" />
          </div>
        )
      }
    ];
    return (
    	<div>
        <div className="container">
          <Button size="default" mb={20} buttonContent={<Link to="./create">新建客服消息</Link>} />
          <Table size="middle" rowKey={record => record._id} columns={columns} dataSource={randomMessageList} />
        </div>
      </div>
    )
  }
}

const mapState = state => ({
  randomMessageList: state.message.randomMsg.randomMessageList
});

const mapDispatch = dispatch => ({
  getRandomMessageList() {
    dispatch(actionCreators.getRandomMessageList())
  },
  deleteRandomMessage(id) {
    dispatch(actionCreators.deleteRandomMessage(id))
  }
});

export default connect(mapState, mapDispatch)(RandomKefu);