import axios from '../../../../utils/http';
import * as constants from './constants';
import { Modal, message } from 'antd';
import backTop from "../../../../utils";

// 群发公号
const getData = (page = 1) => {
  return new Promise((resolve, reject) => {
    axios.get("/novelLinks/show",{params: {page}})
      .then(res => {
        const { data: novelLinkList, domain_name, count } = res.data;
        const action = {
          type: constants.GET_NOVEL_LINK_LIST,
          novelLinkList, domain_name, count
        };
        resolve(action)
      })
  });
};

export const getNovelLinkList = () => {
  return async dispatch => {
    let action = await getData();
    dispatch(action);
  }
};

export const changePage = (page) => {
  return async dispatch => {
    dispatch(await getData(page));
    const action = {
      type: constants.CHANGE_PAGE,
      page
    };
    await backTop();
    await dispatch(action);
  }
};

export const deleteNovel = (id, index) => {
  return dispatch => {
    Modal.confirm({
      title: "确定删除这条链接吗？",
      onOk: () => {
        axios.post("/novelLinks/delete_one", {id: id})
          .then(res => {
            if(res.data.message) {
              message.info(res.data.message);
              const action = {
                type: constants.DELETE_NOVEL,
                index
              };
              dispatch(action);
            }
          });
      }
    })
  }
};

export const changeZIndexState = (zIndex, id) => {
  console.log(zIndex, id)
  return dispatch => {
    if(!zIndex) {
      axios.post('/novelLinks/cancelGoTop', {id})
        .then(res => {
          if(res.data.success) {
            message.success(res.data.success);
            dispatch(getNovelLinkList());
          }
        })
    } else {
      axios.post('/novelLinks/goTop', {id})
        .then(res => {
          if(res.data.success) {
            message.success(res.data.success);
            dispatch(getNovelLinkList());
          }
        })
    }
  }
};

// 编辑小说链接详情

export const submitNovel = (record, props) => {
  return dispatch => {
    let query = props.history.location.query;
    let title = query ? "确认保存修改吗？" : "确认创建一条新的小说链接吗？";
    let url = query ? "/novelLinks/update" : "/novelLinks/add";
    let tips = query ? "保存成功": "创建成功";
    if(!query && record._id) {
      axios.get("/novelLinks/get_content", { params: { _id: record._id } })
        .then(res => {
          record = res.data.data;
          record.id = randomString();
        });
    }
    let remarks = record.remarks;
    Modal.confirm({
      title: title,
      onOk: () => {
        axios.post(url, {...record, remarks}).then(async res => {
          if (res.data.err) {
            message.info(res.data.err);
          } else {
            if((!query && !record._id) || (record._id && query)) {
              props.history.goBack();
            }
            message.success(tips);
            dispatch(await getData());
          }
        });
      }
    })
  }
};

export const handleInputChange = (value, name) => ({
  type: constants.HANDLE_INPUT_CHANGE,
  value, name
});

export const uploadCheckedFalse = () => {
  return dispatch => {
    message.error("图片不得超过500k,请压缩后再次上传")
  }
};

export const initNovelInfo = record => ({
  type: constants.INIT_NOVEL_INFO,
  record
});

// 编辑小说链接

export const getContent = _id => {
  return dispatch => {
    axios.get("/novelLinks/get_content", { params: { _id } })
      .then(res => {
        const { data } = res.data;
        const action = {
          type: constants.GET_CONTENT,
          data
        };
        dispatch(action);
      });
  }
};


export const resetState = () => ({
  type: constants.RESET_STATE
});

export const deleteFinalImg = () => ({
  type: constants.DELETE_FINAL_IMG
});


export const randomString = () => {
  var chars = '0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ';
  var result = '';
  for (var i = 8; i > 0; --i) result += chars[Math.floor(Math.random() * chars.length)];
  return result;
};