import React, { Component } from 'react';
import { Input, message } from 'antd';
import axios from 'axios';

import "./index.scss";

const Search = Input.Search;

class TikTok extends Component {

	constructor(props) {
		super(props);
		this.changeUrl = this.changeUrl.bind(this);
		this.jiexi = this.jiexi.bind(this);
	}

	state = {
		url: "",
		newUrl: ""
	}

	changeUrl(e) {
		let url = e.target.value;
		this.setState({url})
	}

	jiexi(url) {
		if(url) {
			axios({
				method: "get",
				url: "/watermark/douyin",
				params: {url},
				timeout: 50000
			}).then(res => {
				this.setState({newUrl: '/douyin/' + res.data})
			})
		} else {
			message.warning("链接不能为空")
		}
		
	}

  render() {
  	const {url, newUrl} = this.state;
    return (
    	<div className="container">
    		<div className="tik-tok">
          		<Search value={url} placeholder="请输入抖音短视频的链接" size="large" enterButton="解析" onChange={this.changeUrl} onSearch={this.jiexi} />
          		<div className="video-container" style={{display: newUrl ? "block" : "none"}}>
          			<video controlslist="nodownload" controls="controls" muted src={newUrl} autoPlay height="100%" width="100%"></video>
          			<a href={newUrl} download={newUrl} target="__blank">下载</a>
          		</div>
          	</div>
      	</div>
    )
  }
}

export default TikTok;