import * as constants from './constants';
import {randomString} from './actionCreators';

const defaultNovelInfo = {
  type: 0,
  id: randomString(),
  gonghao_id: "",
  pageTitle: "",
  name: "",
  picurl: "",
  capter: "",
  remarks: "",
  imgUrl: "",
  finalImg: "",
  finalImgUrl: "",
  gonghaoLogo: "",
  gonghaoLogoUrl: "",
  domain_name: "https://td.tyuss.com",
  company: "",
  bgcolor: "#fff",
  suffix: "╭(╯ε╰)╮"
};

const defaultState = {
  novelLinkList: [],
  domain_name: "",
  count: null,
  currentPage: 1,
  novelInfo: {...defaultNovelInfo}
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case constants.GET_NOVEL_LINK_LIST:
      newState.novelLinkList = [...action.novelLinkList];
      newState.domain_name = action.domain_name;
      newState.count = action.count;
      return newState;
    case constants.CHANGE_PAGE:
      newState.currentPage = action.page;
      return newState;
    case constants.DELETE_NOVEL:
      newState.novelLinkList.splice(action.index, 1);
      return newState;

    // 编辑小说链接详情

    case constants.HANDLE_INPUT_CHANGE:
      newState.novelInfo[action.name] = action.value;
      if(action.name === "picurl") {
        newState.novelInfo.imgUrl = newState.domain_name + action.value;
      }
      if(action.name === "finalImg") {
        newState.novelInfo.finalImgUrl = newState.domain_name + action.value;
      }
      if(action.name === "gonghaoLogo") {
        newState.novelInfo.gonghaoLogoUrl = newState.domain_name + action.value;
      }
      return newState;

    case constants.INIT_NOVEL_INFO:
      newState.novelInfo = {...action.record};
      newState.novelInfo.imgUrl = newState.domain_name + action.record.picurl;
      newState.novelInfo.finalImgUrl = newState.domain_name + action.record.finalImg;
      newState.novelInfo.gonghaoLogoUrl = newState.domain_name + action.record.gonghaoLogo;
      return newState;
    case constants.GET_CONTENT:
      newState.novelInfo.capter = action.data.capter;
      return newState;
    case constants.DELETE_FINAL_IMG:
      newState.novelInfo.finalImg = newState.novelInfo.finalImgUrl = "";
      return newState;

    case constants.RESET_STATE:
      newState = defaultState;
      return newState;
    default:
      return state;
  }
};