import React, {Component} from 'react';
import {Table, Form, Input, Modal, message, Switch, DatePicker} from 'antd';
import moment from 'moment';
import Button from '../../../common/components/Button';
import axios from '../../../utils/http';

class TimingReply extends Component {

    constructor(props) {
        super(props);
        this.submitReplyInfo = this.submitReplyInfo.bind(this);
        this.cancelAction = this.cancelAction.bind(this);
    }

    state = {
        replyList: [],
        replyModal: false,
        replyInfo: {
            reply_id: "",
            is_nickname: false,
            content: "",
            timing_time: new Date(),
            remarks: ""
        }
    };

    getReplyList() {
        axios.get("/replyTime")
            .then(res => {
                const {code, data} = res.data;
                if(code === 1) {
                    this.setState({replyList: data})
                } else {
                    this.setState({replyList: []})
                }
            })
    }

    openUpdateModal(record) {
        this.setState({
            replyModal: true,
            replyInfo: {...record}
        })
    }

    changeReplyInfoInput(value, name) {
        let {replyInfo} = this.state;
        replyInfo[name] = value;
        this.setState({replyInfo})
    }

    createReplyInfo(replyInfo) {
        console.log(replyInfo)
        Modal.confirm({
            title: "确定创建一条新内容吗？",
            onOk: () => {
                axios.post('/replyTime/create', {...replyInfo})
                    .then(res => {
                        let {code, msg} = res.data;
                        if(code === 1) {
                            message.success(msg);
                            this.getReplyList();
                            this.cancelAction()
                        } else {
                            message.error(msg)
                        }
                    })
            }
        })
    }

    updateReplyInfo(replyInfo) {
        console.log(replyInfo)
        Modal.confirm({
            title: "确定修改这条内容吗？",
            onOk: () => {
                axios.post('/replyTime/update', {...replyInfo})
                    .then(res => {
                        let {code, msg} = res.data;
                        if(code === 1) {
                            message.success(msg);
                            this.getReplyList();
                            this.cancelAction()
                        } else {
                            message.error(msg)
                        }
                    })
            }
        })
    }

    submitReplyInfo() {
        let {replyInfo} = this.state;
        if(!replyInfo._id) {
            this.createReplyInfo(replyInfo)
        } else {
            this.updateReplyInfo(replyInfo)
        }
    }

    cancelAction() {
        this.setState({
            replyModal: false,
            replyInfo: {
                reply_id: "",
                is_nickname: false,
                content: "",
                timing_time: + new Date()
            }
        })
    }

    deleteReply(_id) {
        Modal.confirm({
            title: "确定删除这条内容吗？",
            onOk: () => {
                axios.get("/replyTime/del", {params: {_id}})
                    .then(res => {
                        let {code, msg} = res.data;
                        if(code === 1) {
                            this.getReplyList()
                            message.success(msg)
                        } else {
                            message.error(msg)
                        }
                    })
            }
        })
    }

    componentDidMount() {
        this.getReplyList()
    }

    render() {

        let {replyList, replyModal, replyInfo} = this.state;

        const columns = [
            {
                key: "reply_id",
                dataIndex: "reply_id",
                title: "自动回复消息ID",
                align: "center",
                width: 120
            }, {
                key: "remarks",
                dataIndex: "remarks",
                title: "消息名称",
                align: "center",
                width: 150
            }, {
                key: "timing_time",
                dataIndex: "timing_time",
                title: "设置时间",
                align: "center",
                width: 180,
                render: text => moment(text).format("YYYY-MM-DD HH:mm:ss")
            }, {
                key: "is_nickname",
                dataIndex: "is_nickname",
                title: "开启用户昵称",
                align: "center",
                width: 80,
                render: text => text ? "开启" : "关闭"
            }, {
                key: "content",
                dataIndex: "content",
                title: "回复消息",
                align: "center",
                ellipsis: true
            }, {
                key: "action",
                title: "操作",
                align: "center",
                render: (text, record, index) => (
                    <div>
                        <Button onClick={() => this.openUpdateModal(record)} buttonContent="编辑" />
                        <Button onClick={() => this.deleteReply(record._id)} background="danger" buttonContent="删除" />
                    </div>
                )
            }
        ];
        const formItemConfig = {
            labelCol: {
                span: 4
            }, 
            wrapperCol: {
                span: 16,
                offser: 0
            }
        }
        return (
            <div className="container">
                <Button mb={20} size="default" onClick={() => this.setState({replyModal: true})} buttonContent="新建消息" />
                <Table size="small" dataSource={replyList} rowKey={record => record._id} columns={columns} />
                <Modal visible={replyModal} title="编辑回复消息" onOk={this.submitReplyInfo} onCancel={this.cancelAction}>
                    <Form {...formItemConfig}>
                        <Form.Item label="消息备注">
                            <Input value={replyInfo.remarks} placeholder="自行填写，便于区分对应" onChange={e => this.changeReplyInfoInput(e.target.value, "remarks")} />
                        </Form.Item>
                        <Form.Item label="消息ID">
                            <Input value={replyInfo.reply_id} placeholder="请在自动回复中复制" onChange={e => this.changeReplyInfoInput(e.target.value, "reply_id")} />
                        </Form.Item>
                        <Form.Item label="开启昵称">
                            <Switch checked={replyInfo.is_nickname} onChange={value => this.changeReplyInfoInput(value, "is_nickname")} />
                        </Form.Item>
                        <Form.Item label="回复内容">
                            <Input.TextArea value={replyInfo.content} spellCheck={false} autoSize={{minRows: 3}}
                            onChange={e => this.changeReplyInfoInput(e.target.value, "content")} />
                        </Form.Item>
                        <Form.Item label="设置时间">
                            <DatePicker showTime value={moment(replyInfo.timing_time)} onChange={value => this.changeReplyInfoInput(value, "timing_time")} />
                        </Form.Item>
                    </Form>
                </Modal>   
            </div>
        )
    }
};

export default TimingReply;