import React, { Component, Fragment } from 'react';
import { connect } from "react-redux";
import { Form, Button, Input, Radio, Icon, Upload, Modal, Checkbox, Switch, Select } from "antd";
import * as actionCreators from '../store/actionCreators';
import { Editor } from '@tinymce/tinymce-react';
import zh_CN from '@tinymce/langs/zh_CN.js';

const { Option } = Select;

class EditNovelInfo extends Component {

	componentDidMount() {
		let query = this.props.history.location.query;
		query && this.props.initNovelInfo(query);
		query && this.props.getContent(query._id);
	}

	state = {
		deleteModalStyle: { position: "absolute", top: 0, left: 0, width: 100, height: 100, background: "rgba(0, 0, 0, .3)", color: "#fff", fontSize: 20, display: "none", boxSizing: "border-box" }
	};

	enterImg = e => {
		e.stopPropagation();
		this.setState(state => ({
			deleteModalStyle: { ...state.deleteModalStyle, display: "block" }
		}))
	};

	leaveImg = e => {
		e.stopPropagation();
		this.setState(state => ({
			deleteModalStyle: { ...state.deleteModalStyle, display: "none" }
		}))
	};

	render() {
		const { novelInfo, uploadImg, uploadChecked, handleInputChange, submitNovel, cancel, deleteFinalImg } = this.props;
		const { deleteModalStyle } = this.state;
		const formItemLayout = {
			labelCol: {
				sm: { span: 8 }
			},
			wrapperCol: {
				sm: { span: 15, offset: 0 }
			},
		};
		let isUploadImg;
		if (novelInfo.picurl) {
			isUploadImg = <img style={{ width: "100px" }} src={novelInfo.imgUrl} alt="" />
		} else {
			isUploadImg = <Button><Icon type="upload" /> 上传图片 </Button>
		}
		let isFinalUploadImg, isGonghaoUploadLogo;
		if (novelInfo.finalImg) {
			isFinalUploadImg = <div style={{ width: 100, height: 100, position: "relative" }} onMouseOver={this.enterImg} onMouseLeave={this.leaveImg} onClick={deleteFinalImg}>
				<img style={{ width: 100, height: 100 }} src={novelInfo.finalImgUrl} alt="" />
				<div className="cursor" style={deleteModalStyle}>
					<Icon style={{ margin: "40px 0 0 40px" }} type="delete" />
				</div>
			</div>
		} else {
			isFinalUploadImg = <Upload name="imageFile" action="/novelLinks/upload" onChange={info => uploadImg(info, "finalImg")} showUploadList={false} beforeUpload={uploadChecked}>
				<Button><Icon type="upload" /> 上传图片 </Button>
			</Upload>
		}
		if (novelInfo.gonghaoLogo) {
			isGonghaoUploadLogo = <img style={{ width: "100px" }} src={novelInfo.gonghaoLogoUrl} alt="" />
		} else {
			isGonghaoUploadLogo = <Button><Icon type="upload" /> 上传图片 </Button>
		}
		
		return (
			<div>
				<div className='container'>
					<div className="title-wrapper">
						<h2>编辑小说内容</h2>
						<div className="fr">
							<Button onClick={() => cancel(this.props)}>取消</Button>
							<Button type="primary" style={{ marginLeft: 10 }} onClick={() => submitNovel(novelInfo, this.props)}>提交</Button>
						</div>
					</div>
					<Form className="fl" style={{ width: "30%", height: 500, overflow: "auto" }} {...formItemLayout}>
						<Form.Item label="类型">
							<Radio.Group value={novelInfo.type} onChange={e => handleInputChange(e.target.value, "type")}>
								<Radio value={0}>头条投放</Radio>
								<Radio value={1}>平台数据</Radio>
							</Radio.Group>
						</Form.Item>
						<Form.Item label="id">
							<Input placeholder="请输入id" value={novelInfo.id} onChange={e => handleInputChange(e.target.value, "id")} />
						</Form.Item>
						<Form.Item label="域名">
							<Input placeholder="请输入域名" value={novelInfo.domain_name} onChange={e => handleInputChange(e.target.value, "domain_name")} />
						</Form.Item>
						<Form.Item label="公号id">
							<Input placeholder="请输入id" value={novelInfo.gonghao_id} onChange={e => handleInputChange(e.target.value, "gonghao_id")} />
						</Form.Item>
						<Form.Item label="公号名称">
							<Input placeholder="请输入公号名称" value={novelInfo.name}
								onChange={e => handleInputChange(e.target.value, "name")} />
						</Form.Item>
						<Form.Item label="公司名称">
							<Input placeholder="请输入公司名称" value={novelInfo.company}
								onChange={e => handleInputChange(e.target.value, "company")} />
						</Form.Item>
						<Form.Item label="备注">
							<Input placeholder="请输入备注名称" value={novelInfo.remarks}
								onChange={e => handleInputChange(e.target.value, "remarks")} />
						</Form.Item>
						<Form.Item label="网页标题">
							<Input placeholder="请输入页面标题" value={novelInfo.pageTitle}
								onChange={e => handleInputChange(e.target.value, "pageTitle")} />
						</Form.Item>
						<Form.Item label="随机后缀">
							<Input placeholder="请输入随机后缀，以英文逗号隔开" value={novelInfo.suffix} onChange={e => handleInputChange(e.target.value, "suffix")} />
						</Form.Item>
						<Form.Item label="背景颜色">
							<Input placeholder="请输入背景颜色" value={novelInfo.bgcolor}
								onChange={e => handleInputChange(e.target.value, "bgcolor")} />
						</Form.Item>
						<Form.Item label="顶部banner">
							<Upload name="imageFile" action="/novelLinks/upload" onChange={info => uploadImg(info, "picurl")} showUploadList={false} beforeUpload={uploadChecked}>
								{isUploadImg}
							</Upload>
						</Form.Item>
						<Form.Item label="公号logo">
							<Upload name="imageFile" action="/novelLinks/upload" onChange={info => uploadImg(info, "gonghaoLogo")} showUploadList={false} beforeUpload={uploadChecked}>
								{isGonghaoUploadLogo}
							</Upload>
						</Form.Item>
						<Form.Item label="底部教程图片">
							{isFinalUploadImg}
						</Form.Item>
					</Form>
					<div className="fr" style={{ width: "68%" }}>
						<Editor
							apiKey="yqk1vsq8qil9z824fv4fverb6jo24twdve8d2a8ntgmxlz09"
							initialValue={novelInfo.capter}
							value={novelInfo.capter}
							init={{
								menubar: false,
								language: 'zh_CN',
								language_url : '/langs/zh_CN.js',
								plugins: 'print preview searchreplace autolink directionality visualblocks visualchars image link media template code codesample table charmap hr pagebreak nonbreaking anchor insertdatetime advlist lists wordcount imagetools textpattern help emoticons autosave ',
								toolbar: [{
									name: 'history', items: ['code', 'undo', 'redo']
								}, {
									name: 'styles', items: [ 'fontselect', 'styleselect', 'fontsizeselect', 'h1', 'h2', 'h3', 'h4', 'h5', 'h6' ]
								}, {
									name: 'formatting', items: ['forecolor', 'backcolor', 'bold', 'italic', 'underline', 'strikethrough']
								}, {
									name: 'alignment', items: ['alignleft', 'aligncenter', 'alignright', 'alignjustify']
								}, {
									name: 'symbol', items: ['link', 'anchor', 'blockquote']
								}, {
									name: 'indentation', items: ['outdent', 'indent']
								}, {
									name: 'list', items: ['bullist', 'numlist']
								}, {
									name: 'more', items: ['table', 'image', 'media', 'charmap', 'emoticons', 'hr', 'pagebreak', 'insertdatetime', 'print', 'preview']
								}],
								min_height: 500,
								fontsize_formats: '12px 14px 16px 18px 24px 36px 48px 56px 72px',
								font_formats: '微软雅黑=Microsoft YaHei,Helvetica Neue,PingFang SC,sans-serif;苹果苹方=PingFang SC,Microsoft YaHei,sans-serif;宋体=simsun,serif;仿宋体=FangSong,serif;黑体=SimHei,sans-serif;楷体=楷体;隶书=隶书;幼圆=幼圆;Arial=arial,helvetica,sans-serif;Arial Black=arial black,avant garde;Book Antiqua=book antiqua,palatino;Comic Sans MS=comic sans ms,sans-serif;Courier New=courier new,courier;Tahoma=tahoma,arial,helvetica,sans-serif;Times New Roman=times new roman,times;Verdana=verdana,geneva;Webdings=webdings;Wingdings=wingdings,zapf dingbats;',
								toolbar_drawer: "sliding",
							}}
							onChange={e => handleInputChange(e.target.getContent(), "capter")}
						/>
					</div>
				</div>
			</div>
		)
	}

	componentWillUnmount() {
		this.props.resetState()
	}
}

const mapState = state => ({
	novelInfo: state.channel.links.novelInfo,
});

const mapDispatch = dispatch => ({
	uploadImg(info, name) {
		if (info.file.status === 'done') {
			let url = `/images/website/${info.file.response.filename}`;
			dispatch(actionCreators.handleInputChange(url, name));
		}
	},
	uploadChecked(file) {
		return new Promise((resolve, reject) => {
			if(file.size >= 500 * 1024) {
				dispatch(actionCreators.uploadCheckedFalse());
				reject(false)
			}
			resolve(true)
		})
	},
	handleInputChange(value, name) {
		dispatch(actionCreators.handleInputChange(value, name))
	},
	cancel(props) {
		props.history.goBack();
		dispatch(actionCreators.resetState())
	},
	submitNovel(novelInfo, props) {
		dispatch(actionCreators.submitNovel(novelInfo, props))
	},
	initNovelInfo(record) {
		dispatch(actionCreators.initNovelInfo(record))
	},
	getContent(_id) {
		dispatch(actionCreators.getContent(_id))
	},
	resetState() {
		dispatch(actionCreators.resetState())
	},
	deleteFinalImg() {
		dispatch(actionCreators.deleteFinalImg())
	}
});

export default connect(mapState, mapDispatch)(Form.create({ name: 'editNovelInfo' })(EditNovelInfo));