import React, { Component } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Spread from './spread';
import EditSpread from './spread/edit';

import Links from "./links";
import EditLinks from "./links/edit";

import Management from './management';

import ToutiaoLiantiao from './liantiao';

import Zsy from './zsy';

class ChannelWrapper extends Component {
	render() {
		return (
			<Route path="/admin/channel" render={
				() =>
					<Switch>
						<Route path="/admin/channel/novelLinks/index" component={Links} />
						<Route path="/admin/channel/novelLinks/create" component={EditLinks} />
						<Route path="/admin/channel/novelLinks/update" component={EditLinks} />
						<Route path="/admin/channel/management" component={Management} />
						<Route path="/admin/channel/jointDebugging" component={ToutiaoLiantiao} />
						<Route path="/admin/channel/zhuishuyun" component={Zsy} />
						<Route path="/admin/channel/spread/index" component={Spread} />
						<Route path="/admin/channel/spread/create" component={EditSpread} />
						<Route path="/admin/channel/spread/update" component={EditSpread} />
						<Redirect from="/admin/channel" exact to='/admin/channel/novelLinks' />
						<Redirect from="/admin/channel/novelLinks" exact to='/admin/channel/novelLinks/index' />
					</Switch>
			}
			/>
		)
	}
}

export default ChannelWrapper;