import React, { Component } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import TikTok from './tikTok';
class ToolsRouter extends Component {
  render() {
    return (
    	<Switch>
	        <Route path="/admin/tools/tikTok" component={TikTok} />
	        <Redirect from="/admin/tools" exact to="/admin/tools/tikTok"/>
    	</Switch>
    )
  }
}

export default ToolsRouter;