import * as constants from './constants';

const defaultContent = {
  description: "",
  picurl: "",
  title: "",
  url: "",
  local_picurl: ""
}

const defaultState = {
  randomMessageList: [],
  randomMessageInfo: {
    message_id: "",
    message_array: [],
    remarks: ""
  },
  message_info: {
    contents: [],
    type: 0
  },
  content: {...defaultContent},
  currentIndex: -1
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case constants.GET_RANDOM_MESSAGE_LIST:
      newState.randomMessageList = action.data;
      return newState;

    case constants.CHANGE_MESSAGE_VAlUE:
      newState.randomMessageInfo[action.name] = action.value;
      return newState;

    case constants.CHANGE_MESSAGE_TYPE:
      newState.message_info.type = action.value;
      newState.content = {...defaultContent};
      return newState;

    case constants.CHANGE_CONTENT_VALUE:
      newState.content[action.name] = action.value;
      return newState;

    case constants.ADD_MESSAGE:
      newState.message_info.contents[0] = {...newState.content}
      newState.randomMessageInfo.message_array.push(newState.message_info)
      newState.content = {...defaultContent};
      return newState;

    case constants.UPDATE_MESSAGE_ITEM:
      newState.message_info = action.item;
      newState.content = newState.message_info.contents[0];
      newState.currentIndex = action.index;
      return newState;

    case constants.DELETE_MESSAGE_ITEM:
      newState.randomMessageInfo.message_array.splice(action.index, 1);
      newState.content = {...defaultContent};
      newState.currentIndex = -1;
      return newState;

    case constants.SAVE_MESSAGE_UPDATE:
      newState.message_info.contents[0] = newState.content;
      newState.randomMessageInfo.message_array.splice(newState.currentIndex, 1, newState.message_info);
      newState.content = {...defaultContent};
      newState.currentIndex = -1;
      return newState;

    case constants.RESET_STATE:
      newState = defaultState;
      return newState;

    case constants.INIT_RANDOM_MSG_INFO:
      newState.randomMessageInfo = action.info;
      newState.content = newState.randomMessageInfo.message_array[0].contents[0];
      newState.message_info.type = newState.randomMessageInfo.message_array[0].type;
      newState.currentIndex = 0;
      return newState;

    default:
      return state;
  }
};