import React, { Component } from 'react';
import { Breadcrumb, notification } from 'antd';
import { withRouter } from 'react-router'
import './index.scss';

const breadList = {
  '/gonghao': '公号管理',

  '/gonghao/tuoguan': '公号托管',
  '/gonghao/tuoguan/group': '分组管理',

  '/gonghao/tuoguan/error': '错误信息',

  '/gonghao/tuoguan/config': '公号配置',
  '/gonghao/tuoguan/config/create': '新建配置',
  '/gonghao/tuoguan/config/update': '修改配置',

  '/gonghao/menu': '配置菜单',
  '/gonghao/menu/create': '新建菜单',
  '/gonghao/menu/update': '修改菜单',

  '/gonghao/reply': '自动回复',
  '/gonghao/reply/create': '新建素材',
  '/gonghao/reply/update': '修改素材',

  '/gonghao/timingReply': '定时设置自动回复',

  '/message': '消息管理',

  '/message/kefu': '客服消息',
  '/message/kefu/create': '新建消息',
  '/message/kefu/update': '修改消息',
  
  '/message/randomKefu/index': '随机客服消息',
  '/message/randomKefu/create': '新建随机客服消息',
  '/message/randomKefu/update': '修改随机客服消息',

  '/message/group': '群发消息',
  '/message/group/material': '群发素材',
  '/message/group/history': '历史记录',

  '/message/template': '模板消息',
  '/message/template/create': '创建消息',
  '/message/template/update': '修改消息',

  '/channel': "投放推广",

  '/channel/novelLinks': '投放链接',
  '/channel/novelLinks/create': '创建链接',
  '/channel/novelLinks/update': '修改链接',

  '/channel/management': '投放渠道',

  '/channel/jointDebugging': '头条联调',

  '/channel/zhuishuyun': '追书云链接',
  
  '/channel/spread': '推广链接',
  '/channel/spread/create': '创建素材',
  '/channel/spread/update': '修改素材',

  '/source': '素材库',
  '/source/group': '图文素材',
  '/source/group/create': '新建素材',
  '/source/group/update': '修改素材',

  '/wechat/qrcode': '参数二维码',
  '/wechat/qrcode/create': '新增二维码',
  '/wechat/qrcode/update': '修改二维码',

  '/tools': '便捷工具',
  '/tools/tikTok': '抖音去水印',

  '/account': '账户管理',
  '/account/list': '账户列表',
  '/account/add': '新增账户',
};

class BreadWrapper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pathSnippets: null,
      extraBreadcrumbItems: null,
    }
  }

  getPath = () => {
    this.setState({
      pathSnippets: window.location.pathname.split('/').slice(2)
    });
    this.setState((prev) => {
      return {
        extraBreadcrumbItems: prev.pathSnippets.map((_, index) => {
          let url = `/${prev.pathSnippets.slice(0, index + 1).join('/')}`;
          return (
            <Breadcrumb.Item key={url}>
              {breadList[url]}
            </Breadcrumb.Item>
          );
        })
      }
    })
  };

  seeDetail = () => {
    notification.info({
      message: "系统服务异常公告",
      description: "于2020年1月21日22：30分左右平台出现服务异常，部分功能故障，已于当日23:50排查修复，请各位小伙伴知悉。给大家带来不便，敬请谅解。如因担心再次故障影响使用，请自行在微信公众后台解除授权。",
      style: {
        width: 600,
        marginLeft: 335 - 600,
      },
    })
  }

  UNSAFE_componentWillReceiveProps () {
    this.getPath();
  }

  componentDidMount() {
    this.getPath();
  }

  render() {
    const { extraBreadcrumbItems } = this.state;
    return (
    	<div className='bread-wrapper'>
    		<div className='bread-nav'>
          <Breadcrumb>
            {extraBreadcrumbItems}
          </Breadcrumb>
        </div>
        <div className="notice">
          <span className="notice-title">【系统通知】</span>
          <span onClick={this.seeDetail} className="notice-desc textEllipsis">
            于2020年1月21日22：30分左右平台出现服务异常，部分功能故障，已于当日23:50排查修复，请各位小伙伴知悉。给大家带来不便，敬请谅解。如因担心再次故障影响使用，请自行在微信公众后台解除授权。
          </span>
        </div>
    	</div>
    )
  }

}

export default withRouter(BreadWrapper);