import React, { Component } from 'react';
import { Table, Modal, message, Switch, Input } from 'antd';
import { connect } from "react-redux";
import { actionCreators } from './store';
import CopyToClipboard from 'react-copy-to-clipboard';
import { Link } from 'react-router-dom';
import Button from '../../../common/components/Button';

import "./index.scss";

class NovelLink extends Component {

	componentDidMount() {
		this.props.getNovelLinkList();
	}

	render() {
		const { novelLinkList, count, changePage, deleteNovel, changeZIndexState, copyNovel } = this.props;
		const novelLinkHeader = [
			{
				key: "id",
				dataIndex: "id",
				title: "id",
				align: "center",
				ellipsis: true,
			}, {
				key: "type",
				dataIndex: "type",
				title: "类型",
				align: "center",
				width: 85,
				render: text => !text ? "头条投放" : "平台数据"
			}, {
				key: "name",
				dataIndex: "name",
				title: "公号",
				align: "center",
				width: 85
			}, {
				key: "domain_name",
				dataIndex: "domain_name",
				title: "域名",
				align: "center",
				ellipsis: true,
			}, {
				key: "remarks",
				dataIndex: "remarks",
				title: "备注",
				align: "center",
				ellipsis: true,
			}, {
				key: "zIndex",
				dataIndex: "zIndex",
				title: "置顶状态",
				align: "center",
				width: 85,
				render: (text, record) => <Switch	checked={!!text} checkedChildren="已置顶" unCheckedChildren="默认" onChange={value => changeZIndexState(value, record._id)} />
			}, {
				title: "复制链接",
				align: "center",
				width: 85,
				render: (text, record) => <CopyToClipboard text={record.domain_name + "/tuiguang/" + (record.type === 0 ? "toutiao/" : "data/") + record.id} onCopy={() => message.success("复制成功")}>
					<Button background="success" buttonContent="复制链接" />
				</CopyToClipboard>
			}, {
				title: "Action",
				align: "center",
				render: (text, record, index) => <div>
					<Button background="update" buttonContent={<Link to={{ pathname: "./update", query: record }}>修改</Link>} />
					<Button background="danger" buttonContent="删除" onClick={() => deleteNovel(record.id, index)} />
					<Button background="success" mr={0} buttonContent="复制一条" onClick={() => copyNovel(record, this.props)} />
				</div>
			}
		];

		const pagination = {
			pageSize: 20,
			total: count,
			onChange: page => changePage(page),
		};
		return (
			<div>
				<div className="container">
					<Button background="success" mb={10} size="default" buttonContent={<Link to="./create">新建小说链接</Link>} />
					<Table tableLayout="auto" bordered size="small" rowKey={record => record._id} columns={novelLinkHeader} rowClassName={record => record.zIndex !== 0 ? "selectedRow" : ""} dataSource={novelLinkList} pagination={{ ...pagination }} />
				</div>
			</div>
		)
	}

}

const mapState = state => ({
	novelLinkList: state.channel.links.novelLinkList,
	count: state.channel.links.count
});

const mapDispatch = dispatch => ({
	getNovelLinkList() {
		dispatch(actionCreators.getNovelLinkList())
	},
	changePage(page) {
		dispatch(actionCreators.changePage(page))
	},
	deleteNovel(id, index) {
		dispatch(actionCreators.deleteNovel(id, index))
	},
	changeZIndexState(zIndex, id) {
		dispatch(actionCreators.changeZIndexState(zIndex, id))
	},
	copyNovel(record, props) {
		dispatch(actionCreators.submitNovel(record, props));
	}
});

export default connect(mapState, mapDispatch)(NovelLink);