import React, { Component } from 'react';
import { Modal } from 'antd'
import { connect } from "react-redux";
import { actionCreators } from '../store';

import './index.scss';

class StatisticsModal extends Component {

    render() {
        const { statisticsModal, statistics, closeStatisticsModal } = this.props;
        return (
            <Modal visible={statisticsModal} title={`${statistics.nick_name || ""} 的粉丝数据`} onCancel={closeStatisticsModal} footer="">
                <div className="statistics-modal">
                <div className="statistics-wrapper">
                        <h2 className="title">当日数据统计</h2>
                        <ul className="statistics-content">
                            <li>
                                <span className="category">粉丝总数: </span>
                                <span className="desc">{statistics.cumulate_user}</span>
                            </li>
                            <li>
                                <span className="category">新增粉丝数: </span>
                                <span className="desc">{statistics.new_user}</span>
                            </li>
                            <li>
                                <span className="category">取关粉丝数: </span>
                                <span className="desc">{statistics.cancel_user}</span>
                            </li>
                            <li>
                                <span className="category">活跃粉丝数: </span>
                                <span className="desc">{statistics.todayCount}</span>
                            </li>
                        </ul>
                    </div>
                    <div className="statistics-wrapper">
                        <h2 className="title">昨日数据统计</h2>
                        <ul className="statistics-content">
                            <li>
                                <span className="category">粉丝总数: </span>
                                <span className="desc">{statistics.y_cumulate_user}</span>
                            </li>
                            <li>
                                <span className="category">新增粉丝数: </span>
                                <span className="desc">{statistics.y_new_user}</span>
                            </li>
                            <li>
                                <span className="category">取关粉丝数: </span>
                                <span className="desc">{statistics.y_cancel_user}</span>
                            </li>
                            <li>
                                <span className="category">活跃粉丝数: </span>
                                <span className="desc">{statistics.yesterdayCount}</span>
                            </li>
                        </ul>
                    </div>
                </div>
            </Modal>
        )
    }
};


const mapState = state => ({
    statistics: state.gonghao.tuoguan.statistics,
    statisticsModal: state.gonghao.tuoguan.statisticsModal
});

const mapDispatch = dispatch => ({
    closeStatisticsModal() {
        dispatch(actionCreators.closeStatisticsModal());
    },
});

export default connect(mapState, mapDispatch)(StatisticsModal);